<template>
  <div id="orStat">
    <div>
      <label for>时间范围</label>
      <el-date-picker
        class="timeselect"
        v-model="value1"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      ></el-date-picker>
      <el-button @click="queryBtn" type="primary">查询</el-button>
    </div>
    <div id="charts">123</div>
  </div>
</template>

<script>
import echarts from "echarts"; //引入echarts
import { API_GET_ORDER_REPORT } from "@/API/apis.js"; //引入接口文件
export default {
  data() {
    return {
      value1: [],
      value2: []
    };
  },
  mounted() {
    //初始化容器
    var homeEacharts = echarts.init(document.getElementById("charts"));
   //  console.log(JSON.stringify(this.value1));
      Date.prototype.format = function(fmt) {
        //
        var o = {
          "M+": this.getMonth() + 1, //月份
          "d+": this.getDate(), //日
          "h+": this.getHours(), //小时
          "m+": this.getMinutes(), //分
          "s+": this.getSeconds(), //秒
          "q+": Math.floor((this.getMonth() + 3) / 3), //季度
          S: this.getMilliseconds() //毫秒
        };
        if (/(y+)/.test(fmt))
          fmt = fmt.replace(
            RegExp.$1,
            (this.getFullYear() + "").substr(4 - RegExp.$1.length)
          );
        for (var k in o)
          if (new RegExp("(" + k + ")").test(fmt))
            fmt = fmt.replace(
              RegExp.$1,
              RegExp.$1.length == 1
                ? o[k]
                : ("00" + o[k]).substr(("" + o[k]).length)
            );
        return fmt;
      };
      this.value1.forEach(item => {
        JSON.stringify(
          this.value2.push(new Date(item).format("yyyy-MM-dd hh:mm:ss"))
        );
      });
      API_GET_ORDER_REPORT(JSON.stringify(this.value2)).then(res => {
        if (res) {
          this.value2 = [];
          let option = {
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "cross",
                crossStyle: {
                  color: "#999"
                }
              }
            },
            toolbox: {
              feature: {
                dataView: { show: true, readOnly: false },
                magicType: { show: true, type: ["line", "bar"] },
                restore: { show: true },
                saveAsImage: { show: true }
              }
            },
            legend: {
              data: ["蒸发量", "降水量", "平均温度"]
            },
            xAxis: [
              {
                type: "category",
                data: [
                  "1月",
                  "2月",
                  "3月",
                  "4月",
                  "5月",
                  "6月",
                  "7月",
                  "8月",
                  "9月",
                  "10月",
                  "11月",
                  "12月"
                ],
                axisPointer: {
                  type: "shadow"
                }
              }
            ],
            yAxis: [
              {
                type: "value",
                name: "水量",
                min: 0,
                max: 250,
                interval: 50,
                axisLabel: {
                  formatter: "{value} ml"
                }
              },
              {
                type: "value",
                name: "温度",
                min: 0,
                max: 25,
                interval: 5,
                axisLabel: {
                  formatter: "{value} °C"
                }
              }
            ],
            series: [
              {
                name: "蒸发量",
                type: "bar",
                data: [
                  2.0,
                  4.9,
                  7.0,
                  23.2,
                  25.6,
                  76.7,
                  135.6,
                  162.2,
                  32.6,
                  20.0,
                  6.4,
                  3.3
                ]
              },
              {
                name: "降水量",
                type: "bar",
                data: [
                  2.6,
                  5.9,
                  9.0,
                  26.4,
                  28.7,
                  70.7,
                  175.6,
                  182.2,
                  48.7,
                  18.8,
                  6.0,
                  2.3
                ]
              },
              {
                name: "平均温度",
                type: "line",
                yAxisIndex: 1,
                data: [
                  2.0,
                  2.2,
                  3.3,
                  4.5,
                  6.3,
                  10.2,
                  20.3,
                  23.4,
                  23.0,
                  16.5,
                  12.0,
                  6.2
                ]
              }
            ]
          };
          homeEacharts.setOption(option);
        }
      });
  },
  methods: {
    queryBtn() {
     
    }
  }
};
</script>

<style lang="less" scoped>
#orStat {
  padding: 15px 0 0 15px;
  .timeselect {
    margin: 0 10px 0 10px;
  }
  #charts {
  margin-top: 40px;

  height: 400px;
  background-color: #fff;
  border-radius: 8px;
  padding: 10px;
}
}

</style>